import React, { Fragment, useContext } from "react";
import {
  Typography,
  Paper,
  Grid,
  Divider,
  Hidden,
  List,
  ListItem,
  ListItemText,
  Box,
  Avatar,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import AuthContext from "../components/context/auth/authContext";
import NotApproved, { FileEmpty } from "../components/layout/NotAvailable";
import Titles, { Subtitles } from "../components/layout/Titles";
import MySkeleton, { FileBox } from "../components/layout/Skeletons";
import { UploadButton } from "../components/layout/Upload";
import DashboardWrapper from "../components/layout/Wrapper/DashboardWrapper";
import Dashboard from "../components/routing/Dashboard";
import { Redirect } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(2),
    "& span": {
      padding: theme.spacing(0),
      paddingLeft: theme.spacing(1),
      lineHeight: 1.5,
    },
    "& h3": {
      paddingTop: theme.spacing(0),
      lineHeight: 1,
    },
  },
  subheading: {
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(2),
    marginLeft: theme.spacing(1),
    color: "#777777",
  },
  section: {
    marginTop: theme.spacing(1),
  },
  list: {
    "& span": {
      fontSize: "0.875rem",
      color: "rgba(0,0,0,0.54)",
    },
    "& p": {
      fontSize: "1rem",
      color: "rgba(0,0,0,0.87)",
    },
  },
}));

const role = "society";

const Profile = (props) => {
  const classes = useStyles();
  const authContext = useContext(AuthContext);
  const page = Dashboard();
  if (authContext.userRole && authContext.userRole !== role)
    return <Redirect to={page} />;
  if (authContext.loading) {
    return (
      <DashboardWrapper selectedIndex={0}>
        <Titles title="Society Profile" />
        <Subtitles>Basic</Subtitles>
        <MySkeleton height={3} />
        <Grid className={classes.section} container spacing={3} direction="row">
          <Grid item xs={12} md={4}>
            <MySkeleton height={2.5} />
          </Grid>
          <Grid item xs={12} md={4}>
            <MySkeleton height={2.5} />
          </Grid>
          <Grid item xs={12} md={4}>
            <MySkeleton height={2.5} />
          </Grid>
        </Grid>
        <Subtitles>Registration</Subtitles>
        <MySkeleton height={2} />
        <Subtitles>Audit</Subtitles>
        <MySkeleton height={2} />
        <Subtitles>Location</Subtitles>
        <MySkeleton height={2} />
      </DashboardWrapper>
    );
  }
  if (authContext.user && !authContext.user.approved) {
    return (
      <DashboardWrapper selectedIndex={0}>
        <Titles title="Society Profile" />
        <NotApproved />
      </DashboardWrapper>
    );
  }

  return (
    <DashboardWrapper selectedIndex={0}>
      <Titles title="Society Profile" />

      <Subtitles>Basic</Subtitles>

      <Paper className={classes.section} elevation={2}>
        <List className={classes.list}>
          <ListItem>
            <ListItemText
              primary="Name"
              secondary={
                authContext.user && authContext.user.name
                  ? authContext.user.name
                  : "--"
              }
            />
          </ListItem>
          <Divider />
          <ListItem>
            <ListItemText
              primary="Industry"
              secondary={
                authContext.details && authContext.details.industry
                  ? authContext.details.industry
                  : "--"
              }
            />
          </ListItem>
          <Divider />
          <ListItem>
            <ListItemText
              primary="Class"
              secondary={
                authContext.details && authContext.details.class
                  ? authContext.details.class
                  : "--"
              }
            />
          </ListItem>
        </List>
      </Paper>
      <Grid className={classes.section} container spacing={3} direction="row">
        <Grid item xs={12} md={4}>
          <Paper elevation={2} className={classes.paper}>
            <Grid container direction="column">
              <Typography variant="overline" color="initial">
                Active
              </Typography>
              <Grid container direction="row" alignItems="center">
                <Typography variant="h3">
                  {authContext.details && authContext.details.members
                    ? authContext.details.members.filter(
                        (member) => member.active
                      ).length
                    : "--"}
                </Typography>
                <Typography variant="caption">Members</Typography>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
        <Grid item xs={12} md={4}>
          <Paper elevation={2} className={classes.paper}>
            <Grid container direction="column">
              <Typography variant="overline" color="initial">
                Official
              </Typography>
              <Grid container direction="row" alignItems="center">
                <Typography variant="h3">
                  {authContext.details && authContext.details.members
                    ? authContext.details.members.filter(
                        (member) => member.official
                      ).length
                    : "--"}
                </Typography>
                <Typography variant="caption">Members</Typography>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
        <Grid item xs={12} md={4}>
          <Paper elevation={2} className={classes.paper}>
            <Grid container direction="column">
              <Typography variant="overline" color="initial">
                Total
              </Typography>
              <Grid container direction="row" alignItems="center">
                <Typography variant="h3">
                  {authContext.details && authContext.details.members
                    ? authContext.details.members.length
                    : "--"}
                </Typography>
                <Typography variant="caption">Members</Typography>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
      </Grid>

      <Subtitles>Registration</Subtitles>

      <Paper className={classes.section} elevation={2}>
        <List className={classes.list}>
          <Grid container>
            <Grid item xs={12} md={4}>
              <ListItem>
                <ListItemText
                  primary="Number"
                  secondary={
                    authContext.details && authContext.details.regno
                      ? authContext.details.regno
                      : "--"
                  }
                />
              </ListItem>
              <Hidden mdUp>
                <Divider />
              </Hidden>
            </Grid>
            <Grid item xs={12} md={4}>
              <ListItem>
                <ListItemText
                  primary="Date"
                  secondary={
                    authContext.details && authContext.details.dateApproved
                      ? new Date(
                          authContext.details.dateApproved
                        ).toDateString()
                      : authContext.user && authContext.user.dateCreated
                      ? new Date(authContext.user.dateCreated).toDateString()
                      : "--"
                  }
                />
              </ListItem>
              <Hidden mdUp>
                <Divider />
              </Hidden>
            </Grid>
            <Grid item xs={12} md={4}>
              <ListItem>
                <ListItemText
                  primary="Status"
                  secondary={
                    authContext.details && authContext.details.status
                      ? authContext.details.status
                      : "--"
                  }
                />
              </ListItem>
            </Grid>
          </Grid>
        </List>
      </Paper>

      <Subtitles>Audit</Subtitles>

      <Paper className={classes.section} elevation={2}>
        <List className={classes.list}>
          <Grid container>
            <Grid item xs={12} md={4}>
              <ListItem>
                <ListItemText
                  primary="Last Date"
                  secondary={
                    authContext.details && authContext.details.lastAudit
                      ? new Date(authContext.details.lastAudit).toDateString()
                      : "--"
                  }
                />
              </ListItem>
              <Hidden mdUp>
                <Divider />
              </Hidden>
            </Grid>
            <Grid item xs={12} md={4}>
              <ListItem>
                <ListItemText
                  primary="Year"
                  secondary={
                    authContext.details && authContext.details.lastAuditYear
                      ? new Date(
                          authContext.details.lastAuditYear
                        ).getFullYear()
                      : "--"
                  }
                />
              </ListItem>
              <Hidden mdUp>
                <Divider />
              </Hidden>
            </Grid>
            <Grid item xs={12} md={4}>
              <ListItem>
                <ListItemText
                  primary="Class"
                  secondary={
                    authContext.details && authContext.details.auditClass
                      ? authContext.details.auditClass
                      : "--"
                  }
                />
              </ListItem>
            </Grid>
          </Grid>
          <Divider />
          <ListItem>
            <ListItemText
              primary="Paid-up Share Capital"
              secondary={
                authContext.details && authContext.details.paidUpShareCapital
                  ? authContext.details.paidUpShareCapital
                  : "--"
              }
            />
          </ListItem>
        </List>
      </Paper>

      <Subtitles>Location</Subtitles>

      <Paper className={classes.section} elevation={2}>
        <List className={classes.list}>
          <ListItem>
            <ListItemText
              primary="Address"
              secondary={
                authContext.details && authContext.details.address
                  ? authContext.details.address
                  : "--"
              }
            />
          </ListItem>
          <Divider />
          <Grid container>
            <Grid item xs={12} md={4}>
              <ListItem>
                <ListItemText
                  primary="Road/Street"
                  secondary={
                    authContext.details && authContext.details.roadStreet
                      ? authContext.details.roadStreet
                      : "--"
                  }
                />
              </ListItem>
              <Hidden mdUp>
                <Divider />
              </Hidden>
            </Grid>
            <Grid item xs={12} md={4}>
              <ListItem>
                <ListItemText
                  primary="L.G.A."
                  secondary={
                    authContext.details && authContext.details.lga
                      ? authContext.details.lga
                      : "--"
                  }
                />
              </ListItem>
              <Hidden mdUp>
                <Divider />
              </Hidden>
            </Grid>
            <Grid item xs={12} md={4}>
              <ListItem>
                <ListItemText
                  primary="State"
                  secondary={
                    authContext.details && authContext.details.state
                      ? authContext.details.state
                      : "--"
                  }
                />
              </ListItem>
            </Grid>
          </Grid>
        </List>
      </Paper>
    </DashboardWrapper>
  );
};

export const Members = () => {
  const authContext = useContext(AuthContext);
  const members = (authContext.details && authContext.details.members) || [];
  const officials = members.filter((member) => member.role !== "member");
  const regulars = members.filter((member) => member.role === "member");
  const page = Dashboard();
  if (authContext.userRole && authContext.userRole !== role)
    return <Redirect to={page} />;

  if (authContext.loading) {
    return (
      <DashboardWrapper selectedIndex={1}>
        <Titles title="Members" />
        <Subtitles>Officials</Subtitles>
        <MySkeleton height={3} />
        <Subtitles>Members</Subtitles>
        <MySkeleton height={3} />
      </DashboardWrapper>
    );
  }
  if (authContext.user && !authContext.user.approved) {
    return (
      <DashboardWrapper selectedIndex={1}>
        <Titles title="Members" />
        <NotApproved />
      </DashboardWrapper>
    );
  }
  return (
    <DashboardWrapper selectedIndex={1}>
      <Titles title="Members" />

      <Subtitles>{officials.length} Officials</Subtitles>

      <Paper elevation={2}>
        <Box p={3}>
          <Grid container spacing={5}>
            {officials.map((member, index) => {
              return (
                <Grid key={index} item md={4} xs={12}>
                  <Grid container wrap="nowrap" spacing={2}>
                    <Grid item>
                      <Avatar>M</Avatar>
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" color="initial">
                        {member.name}
                      </Typography>
                      <Typography variant="body2" color="initial">
                        {member.email} {member.phone}
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
              );
            })}
          </Grid>
        </Box>
      </Paper>

      <Subtitles>{regulars.length} Members</Subtitles>

      <Paper elevation={2}>
        <Box p={3}>
          <Grid container spacing={5}>
            {regulars.map((member, index) => {
              return (
                <Grid key={index} item md={4} xs={12}>
                  <Grid container wrap="nowrap" spacing={2}>
                    <Grid item>
                      <Avatar>M</Avatar>
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" color="initial">
                        {member.name}
                      </Typography>
                      <Typography variant="body2" color="initial">
                        {member.email} {member.phone}
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
              );
            })}
          </Grid>
        </Box>
      </Paper>
    </DashboardWrapper>
  );
};

export const MandatoryReturns = () => {
  const file = { name: "Everything file", url: "/", dateCreated: Date.now() };
  const files = [];
  if (files && files.length === 0) {
    return (
      <DashboardWrapper selectedIndex={2}>
        <Titles title="Mandatory Returns">
          <UploadButton text="Upload File" />
        </Titles>
        <FileEmpty
          title="No Files Available"
          body="Click the Button above to upload a file."
        />
      </DashboardWrapper>
    );
  }
  return (
    <DashboardWrapper selectedIndex={2}>
      <Titles
        title="Mandatory Returns"
        buttonText="Upload File"
        onClick={() => console.log("clicked")}
      />
      <Box mt={3}>
        <Grid container spacing={3}>
          {files.map((file, index) => {
            return (
              <Grid item key={index} xs={12} md={4}>
                <Paper elevation={2}>
                  <Box p={3}>
                    <FileBox
                      name={file.name}
                      url={file.url}
                      dateCreated={file.dateCreated}
                      type="file"
                    />
                  </Box>
                </Paper>
              </Grid>
            );
          })}
          <Grid item xs={12} md={4}>
            <Paper elevation={2}>
              <Box p={3} alignContent="center">
                <FileBox type="add" name="Upload file" />
              </Box>
            </Paper>
          </Grid>
        </Grid>
      </Box>
    </DashboardWrapper>
  );
};

export const Requests = () => {
  const authContext = useContext(AuthContext);
  const page = Dashboard();
  if (authContext.userRole && authContext.userRole !== role)
    return <Redirect to={page} />;
  return (
    <DashboardWrapper selectedIndex={4}>
      <Titles title="Requests" />
    </DashboardWrapper>
  );
};

export const Audits = () => {
  return (
    <DashboardWrapper selectedIndex={5} selectedSubIndex={0}>
      <Titles title="Audits" />
    </DashboardWrapper>
  );
};

export const Consultancy = () => {
  const authContext = useContext(AuthContext);
  const page = Dashboard();
  if (authContext.userRole && authContext.userRole !== role)
    return <Redirect to={page} />;
  return (
    <DashboardWrapper selectedIndex={5} selectedSubIndex={1}>
      <Titles title="Consultancy" />
    </DashboardWrapper>
  );
};

export const Financial = () => {
  const authContext = useContext(AuthContext);
  const page = Dashboard();
  if (authContext.userRole && authContext.userRole !== role)
    return <Redirect to={page} />;
  return (
    <DashboardWrapper selectedIndex={5} selectedSubIndex={2}>
      <Titles title="Financial" />
    </DashboardWrapper>
  );
};

export const Legal = () => {
  const authContext = useContext(AuthContext);
  const page = Dashboard();
  if (authContext.userRole && authContext.userRole !== role)
    return <Redirect to={page} />;
  return (
    <DashboardWrapper selectedIndex={5} selectedSubIndex={3}>
      <Titles title="Legal" />
    </DashboardWrapper>
  );
};

export default Profile;
