import React, { useContext, useEffect } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import "./App.css";
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import { green, orange } from "@material-ui/core/colors";
import Home from "./pages/Home";
import AboutUs from "./pages/AboutUs";
import News from "./pages/News";
import PageNotFound from "./pages/PageNotFound";
import Login from "./pages/Login";
import Register from "./pages/Register";
import Notifications from "./pages/Notifications";
import Profile, {
  MandatoryReturns,
  Requests,
  Consultancy,
  Financial,
  Legal,
  Audits,
  Members,
} from "./pages/Cooperative";
import SocietyUpdate from "./components/layout/Updates/SocietyUpdate";

import Alerts from "./components/layout/Alerts";
import setAuthToken from "./utils/setAuthToken";

import MomentUtils from "@date-io/moment";
import { MuiPickersUtilsProvider } from "@material-ui/pickers";

import AuthContext from "./components/context/auth/authContext";
import NotificationsContext from "./components/context/notifications/notificationsContext";

import PrivateRoute from "./components/routing/PrivateRouting";

const theme = createMuiTheme({
  palette: {
    primary: {
      main: green[800],
    },
    secondary: {
      main: orange[300],
    },
    bgcolor: {
      main: "#fafafa",
    },
    white: {
      main: "#fff",
    },
  },
});

localStorage.token && setAuthToken(localStorage.token);

function App() {
  const authContext = useContext(AuthContext);
  const notificationsContext = useContext(NotificationsContext);
  useEffect(() => {
    authContext.loadUser();
  }, []);
  useEffect(() => {
    if (authContext.user) {
      notificationsContext.getAllNotifications();
    }
  }, [authContext]);
  return (
    <ThemeProvider theme={theme}>
      <MuiPickersUtilsProvider utils={MomentUtils}>
        <Router>
          <Alerts>
            <Switch>
              <Route exact path="/" component={Home} />
              <Route exact path="/about-us" component={AboutUs} />
              <Route exact path="/404" component={PageNotFound} />
              <Route exact path="/login" component={Login} />
              <Route exact path="/register" component={Register} />
              <Route exact path="/news" component={News} />
              <PrivateRoute exact path="/update" component={SocietyUpdate} />
              <PrivateRoute exact path="/society" component={Profile} />
              <PrivateRoute exact path="/society/members" component={Members} />
              <PrivateRoute
                exact
                path="/society/mandatory_returns"
                component={MandatoryReturns}
              />
              <PrivateRoute
                exact
                path="/society/requests"
                component={Requests}
              />
              <PrivateRoute exact path="/society/audits" component={Audits} />
              <PrivateRoute
                exact
                path="/society/consultancy"
                component={Consultancy}
              />
              <PrivateRoute
                exact
                path="/society/financial"
                component={Financial}
              />
              <PrivateRoute exact path="/society/legal" component={Legal} />
              <PrivateRoute exact path="/auditor" component={Audits} />
              <PrivateRoute
                exact
                path="/notifications"
                component={Notifications}
              />
              <Redirect from="*" to="/404" />
            </Switch>
          </Alerts>
        </Router>
      </MuiPickersUtilsProvider>
    </ThemeProvider>
  );
}

export default App;
